nav{
    display: flex;
    width: 450px;
    font-size: 21px;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    margin-top: 60px;
}

.navPartition{
    height: 34px;
    width: 2px;
    background-color: #212529;
}

.registerBtn{
    font-size: 16px;
    padding: 6px 15px;
    border: 2px solid #f97216;
    border-radius: 18px;
    background-color: white;
}

.registerBtn:hover{
    background-color: #f97216;
    color: white;
}
a{
    text-decoration: none;
    color: #f97216;
}

.employer{
    margin: auto 0;
}