.container{
    display: flex;
    flex-direction: column;
    margin-top: 90px;
    margin-left: -16px;
}

h{
    font-size: 35px;
    margin-bottom: 17px;
}

.desc{
    width: 300px;
    line-height: 1.2;
    margin-top: 15px;
    margin-bottom: 50px;
    color: #5a5865;
}
.internshipData{
    display: flex;
    flex-direction: column;
}

input{
    background-color: #f0f0f0;
    margin: 15px 0px;
    width: 300px;
    border: none;
    border-radius: 14px;
    height: 40px;
    padding-left: 15px;
    padding-right: 65px;
}
.jobTitleDiv{
    position: relative;
    width: fit-content;
}

.locationDiv{
    position: relative;
    width: fit-content;
}

i{
    position: absolute;
    color: #b4b4b4;
    font-size: 20px;
    right: 25px;
    top: 25px;
}
.dataSubmitBtn{
    width: 100px;
    background-color: #f97216;
    border: none;
    border-radius: 19px;
    height: 35px;
    color: white;
    margin-top: 20px;
    margin-left: 190px;
}